.app {
  text-align: center;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px+2vmin);
  color: white;
}
.App-header > h2 {
  margin-bottom: 30px;
}
.cellInput {
  width: 50px;
  height: 50px;
  font-size: 20px;
  text-align: center;
  padding: 0px;
  border: 1px solid black;
}
table {
  border-collapse: collapse;
  border: 3px solid bisque;
  background-color: bisque;
}
td {
  padding: 0;
}

.buttonContainer {
  margin-top: 8vh;
  display: flex;
  gap: 2vw;
}
.solveButton {
  background-color: orange;
  font-weight: bold;
  padding: 12px 20px;
  border: none;
  border-radius: 20px;
  font-size: 14px;
}
.bBorder {
  border-bottom: 2px solid black;
}
.rBorder {
  border-right: 2px solid black;
}
.CheckButton {
  background-color: rgb(255, 72, 0);
  font-weight: bold;
  padding: 12px 20px;
  border: none;
  border-radius: 20px;
  font-size: 14px;
}
.resetButton {
  background-color: rgb(117, 173, 218);
  font-weight: bold;
  padding: 12px 20px;
  border: none;
  border-radius: 20px;
  font-size: 14px;
}
